*,
*::before,
*::after {
  box-sizing: border-box;
  user-select: none;
}

//INPUT

input {
  width: 100%;
}

// IMG

img {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

// LINK

.no-style {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

// HIDE / SHOW

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  opacity: 0;
}
