.user-bar {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 10px;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
}

.user-bar__right {
  margin-right: auto;
  display: grid;
  align-items: center;
  align-content: center;
}
